.collection{
  padding-top: 111px;
  padding-bottom: 22px;
  background-color: var(--mirage-3);

  &__list{
    max-width: 340px;
    display: flex;
    column-gap: 30px;
  }

  &__date{
    margin-top: 13px;
    margin-left: -115px;
  }

  &__content{
    margin-top: 45px;
  }

  &__list-cinema{
    display: none;
  }

  &__item{
    grid-column: 2 span;
  }

  &__list-cinema--active{
    display: grid;
    grid-template-columns: repeat(10,1fr);
    gap: 50px;
  }

  &___tab--active{
    color: var(--white);
  }
}


@media (max-width: 1400px){

  .collection{

     &__menu{
      flex-wrap: wrap;
      row-gap: 10px;
     }

     &__date{
      margin-top: 21px;
      margin-left: 0;
     }

     &__list{
      max-width: 1400px;
      width: 100%;
     }

     &__content{
      margin-top: 20px;
     }

     &__article{
      align-items: center;
      justify-content: normal;
     }

     &__list-cinema--active{
      grid-template-columns: repeat(3,1fr);
      gap: 50px;
    }

    &__item{
      grid-column: 1 span;
    }

  }
 }

 @media (max-width: 800px){

   .collection{

    &__t2{
      width: 100%;
      max-width: 320px;
    }

    &__list-cinema--active{
      grid-template-columns: repeat(2,1fr);
      gap: 50px;
    }

    .card-mini:last-child{
      display: none;
    }
  }
 }


 @media (max-width: 525px){

  .collection{

    &__menu{
      justify-content: center;
    }

    &__t2{
      width: 255px;
    }

    &__list{
      display: none;
    }
  }

 }
